import React, { Component } from 'react';
import { TabBar } from 'antd-mobile';
import HomePage from './HomePage';
import UserCenter from '../UserCenter'
import '../../constants'

class MainPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
          selectedTab: global.constants.homeTab
        };
    }

    render() {
      return (
        <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0, webkitOverflowScrolling: 'touch' }}>
          <TabBar
            unselectedTintColor="#949494"
            tintColor="#FAA0B8"
            barTintColor="white">
            <TabBar.Item
              icon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: 'url(https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_tab_home_nor.png) center center /  21px 21px no-repeat' }} />
              }
              selectedIcon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: 'url(https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_tab_home_sel.png) center center /  21px 21px no-repeat' }} />
              }title="闯关"
              key="home"
              selected={this.state.selectedTab === 'homeTab'}
              onPress={() => {
                global.constants.homeTab = 'homeTab'
                this.setState({
                  selectedTab: 'homeTab',
                });
              }}
            >
             <HomePage />
            </TabBar.Item>
            <TabBar.Item
              icon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: 'url(https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_tab_my_nor.png) center center /  21px 21px no-repeat' }} />
              }
              selectedIcon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: 'url(https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_tab_my_sel.png) center center /  21px 21px no-repeat' }} />
              }
              title="我的"
              key="my"
              selected={this.state.selectedTab === 'myTab'}
              onPress={() => {
                global.constants.homeTab = 'myTab'
                this.setState({
                  selectedTab: 'myTab',
                });
              }} >
              <UserCenter />
            </TabBar.Item>
          </TabBar>
        </div>
      );
    }
}

export default MainPage