import React, { Component } from 'react';
import { List, WingBlank, WhiteSpace } from 'antd-mobile';
import PropTypes from 'prop-types';

class UserCenter extends Component {
    static contextTypes = {
        router: PropTypes.object.isRequired,
    }

    constructor(props) {
        super(props);
        this.onClickPrizeList = this.onClickPrizeList.bind(this)
    }

    onClickPrizeList() {
        this.context.router.history.push('/PrizeList');
    }

    render() {
        return (
            <div style={{background: 'rgba(250,245,247,1.0)', overflow: 'hidden'}}>
                <WingBlank>
                <div style={{textAlign:'center' }}>
                    <img style={{marginTop: '25px', borderRadius: '50px', border: 'none', width: '83px', height: '83px' }} src="http://b.hiphotos.baidu.com/image/pic/item/8435e5dde71190ef772420c0cc1b9d16fcfa60d5.jpg" alt="" />
                    <h2>奥巴马</h2>
                </div>
                <List >
                    <List.Item  extra={<span style={{ color: '#333333', fontSize: '15px' }}>去充值</span>} 
                                thumb="https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_balance.png" multipleLine arrow="horizontal" onClick={() => {}}>
                        <span style={{ color: '#000000', fontSize: '12px' }}>我的余额</span>
                        <List.Item.Brief style={{ color: '#F85668', fontSize: '15px' }}>¥1999元</List.Item.Brief>
                    </List.Item>
                </List>
                <List renderHeader={() => ''}>
                    <List.Item arrow="horizontal"
                    thumb="https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_my_prize.png"
                    onClick={() => {
                        this.onClickPrizeList()  
                    }}>
                       <span style={{ color: '#000000', fontSize: '15px' }}>我的奖品</span>
                    </List.Item>
                    <List.Item arrow="horizontal"
                    thumb="https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_customer_service.png"
                    onClick={() => {}}>
                        <span style={{ color: '#000000', fontSize: '15px' }}>联系客服</span>
                    </List.Item>
                </List>

                <List renderHeader={() => ''}>
                <List.Item arrow="horizontal"
                    thumb="https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_address.png"
                    onClick={() => {}}>
                        <span style={{ color: '#000000', fontSize: '15px' }}>收货地址</span>
                        <List.Item.Brief>
                            
                        </List.Item.Brief>
                    </List.Item>
                <List.Item
                        multipleLine >
                            <span inline style={{ color: '#343434', fontSize: '12px' }}>收件人：</span>
                            <span inline style={{ color: '#343434', fontSize: '12px' }}>太先生</span>
                            <br />
                            <span style={{ color: '#343434', fontSize: '12px' }}>联系方式：</span>
                            <span inline style={{ color: '#343434', fontSize: '12px' }}>13100000000</span>
                            <br />
                            <span style={{ color: '#343434', fontSize: '12px' }}>收件地址：</span>
                            <span inline style={{ color: '#343434', fontSize: '12px' }}>四川省成都市高新区XXXX</span>
                </List.Item>
                </List>
                <WhiteSpace size="lg" />
                </WingBlank>
            </div>
        );
    }
}

export default UserCenter