import React, { Component } from 'react';
import { NoticeBar, WhiteSpace, WingBlank, Icon, Flex, NavBar } from 'antd-mobile';

class HomeHeader extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div>
                <NoticeBar icon={null} style={{background: 'rgba(0,0,0,0.3)'}} marqueeProps={{loop: true, style: { padding: '0 7.5px' } }}>
                    <span style={{ color:"#ffffff" }}>恭喜xxxx成功通关游戏，赢取到yyyy</span>
                </NoticeBar>
                <WhiteSpace size="lg" />
                <WingBlank>
                    <div style= {{width : '100%', height : 'auto', backgroundColor:'#ffffff',borderRadius:'10px', padding: '15px 0', textAlign:'center'}}>
                    <label inline style={{ fontSize:'18px', marginLeft: '4px', marginRight: '8px' }} >已累计送出</label>
                        <p style={{textAlign:'bottom'}}>
                            <label inline style={{ fontSize:'30px' }} >10086</label>
                            <label inline style={{ fontSize:'16px' }} >支口红</label>
                        </p>
                        <Flex justify='center'>
                            <Icon inline type={'check-circle-o'} />
                            <label inline style={{ fontSize:'14px', marginLeft: '4px', marginRight: '8px' }} >全场包邮</label>
                            <Icon inline type={'check-circle-o'} />
                            <label inline style={{ fontSize:'14px', marginLeft: '4px', marginRight: '8px' }} >专柜正品</label>
                            <Icon inline type={'check-circle-o'} />
                            <label inline style={{ fontSize:'14px', marginLeft: '4px' }} >假一赔百</label>
                        </Flex>
                    </div>
                    <Flex justify='center' style={{ marginTop: '16px', flexFlow:'row nowrap', justifyContent: 'space-between' }} >
                        <span style={{color: '#363636', fontSize: '15px', fontWeight: 'bold'}}>连闯三关，口红寄回家!</span>
                        <Flex justify='center'>
                            <img src='https://liuyang890-10060662.cos.ap-shanghai.myqcloud.com/lipstick/icon_game_instructions.png' style={{width : '16px', height : '16px' }} />
                            <span style={{color: '#EA68A2', fontSize: '15px', marginLeft: '5px' }}>闯关说明</span>
                        </Flex>
                    </Flex>
                </WingBlank>
            </div>
        );
    }
}

export default HomeHeader