import React, { Component } from 'react';
import HomeHeader from './HomeHeader'
import HomeGrid from './HomeGrid'
import { WingBlank, NavBar, List } from 'antd-mobile';

const data = Array.from(new Array(15)).map((_val, i) => ({
    icon: 'http://img.zcool.cn/community/0193cc58ccefffa801219c7727a0cc.jpg@900w_1l_2o_100sh.jpg',
    text: `口红${i}`,
  }));

class HomePage extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div style={{background: 'rgba(250,245,247,1.0)', overflow: 'hidden'}}>
                <HomeHeader />
                <WingBlank size="l" style={{marginTop: '20px'}} >
                    <HomeGrid data={data}
                            columnNum={3}
                            itemStyle={{ width: 'auto' ,height: 'auto' }}/>  
                </WingBlank>
            </div>
        );
    }
}

export default HomePage