import React, { Component } from 'react';
import { Flex, WhiteSpace } from 'antd-mobile';
import HomeGridItem from './HomeGridItem'

class HomeGrid extends Component {
    static defaultProps = {
        data: [],
        columnNum: 4,
        itemStyle: {},
    };

    constructor(props) {
        super(props);
    }

    getRows(rowCount, dataLength) {
        const rowsArr = []
        let {
            columnNum,
            data,
            onClick,
            itemStyle,
          } = this.props
        const rowWidth = `${100 / columnNum}%`;
        const colStyle = {
            width: rowWidth,
            ...itemStyle,
        };

        for (let i = 0; i < rowCount; i++) {
            const rowArr = []
            for (let j = 0; j < columnNum; j++) {
                const dataIndex = i * columnNum + j
                let itemEl
                if (dataIndex < dataLength) {
                    const el = data && data[dataIndex]
                    itemEl = (
                          <Flex.Item
                            onClick={() => onClick && onClick(el, dataIndex)}
                            style={colStyle}
                          >
                            <HomeGridItem itemData={el} />
                          </Flex.Item>
                      );
                } else {
                    itemEl = (
                      <Flex.Item
                        key={`griditem-${dataIndex}`}
                        style={colStyle}
                      />
                    );
                }
                rowArr.push(itemEl);
            }
            rowsArr.push(
                <Flex justify="center" align="stretch" key={`gridline-${i}`}>
                  {rowArr}
                </Flex>, <WhiteSpace size="lg" />,
            );
        }
        return rowsArr;
    }

    render() {
        const dataLength = (this.props.data && this.props.data.length) || 0;
        let rowCount = Math.ceil(dataLength / this.props.columnNum);

        let rowsArr;
        let renderEl;

        rowsArr = this.getRows(rowCount, dataLength);
        renderEl = rowsArr;
        return <div>{renderEl}</div>;

        /*return (
            <div>
                <WingBlank size="lg" >
                    <Grid data={this.props.data}
                            columnNum={3}
                            square={false}
                            hasLine={false}
                            activeStyle={false}
                            itemStyle={{ width: 'auto' ,height: 'auto', background: 'rgba(250,245,247,1.0)' }}
                            renderItem={dataItem => (
                                <HomeGridItem icon={dataItem.icon} />
                        )} />  
                </WingBlank>
            </div>
        );*/
    }
}

export default HomeGrid