import React, { Component } from 'react';
import { Button } from 'antd-mobile';

class HomeGridItem extends Component {
    static defaultProps = {
        itemData: [],
    }

    constructor(props) {
        super(props);
    }

    render() {
        let {
            itemData,
          } = this.props
        return (
            <div style={{background: 'rgba(255,255,255,1.0)', overflow: 'hidden'}}>
                <img src={itemData.icon} style={{ width: '100%', height: 'auto' }} alt=""
                        onClick={()=> (
                            console.info("abcdefg")
                        )} />
                <div style={{ paddingLeft: '12px', width: '100%', background: 'rgba(255,255,255,1.0)'}}>
                    <span style={{color: '#3D3D3D', fontSize: '12px' ,display:'block', float:'left', whiteSpace: 'nowrap'}}>迪奥 Diro#999</span>
                    <div style={{display:'block', float:'left', whiteSpace: 'nowrap', marginTop: '4px'}}>
                        <span inline style={{color: '#FC2F2F', fontSize: '10px'}}>¥</span>
                        <span inline style={{color: '#FC2F2F', fontSize: '21px'}}>2元</span>
                        <span inline style={{color: '#f73437', fontSize: '12px', marginLeft: '6px'}}>闯关购</span>
                    </div>
                     <div style={{display:'block', float:'left', whiteSpace: 'nowrap', marginTop: '4px'}}>
                        <span inline style={{color: "#3D3D3D", fontSize: '10px'}}>专柜价</span>
                        <span inline style={{textDecoration:'line-through', color: "#3D3D3D", fontSize: '10px'}}>¥299</span>
                     </div>
                </div>
                <div style={{display:'block', float:'left', whiteSpace: 'nowrap', marginTop: '8px', width: '100%', height: '30px'}}>
                <Button type="warning" size="small" style={{fontSize: '15px', background: 'linear-gradient(90deg, #FC2F2F, #FF678F, #FC2F2F)'}}>立即闯关</Button>
                </div>
            </div>
        );
    }
}

export default HomeGridItem